*
{
    margin:0;
    padding:0;
}
body {
    background: url("https://visme.co/blog/wp-content/uploads/2017/07/50-Beautiful-and-Minimalist-Presentation-Backgrounds-046.jpg");
    font-family: "Bree Serif", serif;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  .heading
  {
    text-align: center;
    color: #ffffff;
  
  }
  .app
  {
    background: linear-gradient(83deg,#ff04c0 0%, hsl(342, 100%, 51%) 80%);
    width: 95%;
    min-height: 200px;
    height: min-content;
    border-radius: 15px;
    padding: 20px;
    box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
    display: flex;
    justify-content: space-evenly;
  }

.Question-section
{
position: relative;
width: 100%;
}
.Question-count
{
  color:rgb(129, 255, 3);
    font-size: 30px;
    margin-bottom: 20px;
}
.Question-count span{
    font-size: 30px;
    color:rgb(129, 255, 3);
}
.Question-text
{
    color: white;
    font-size: 30px;
  font-weight: bold;

}
.Answer-section
{
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: space-between;
}
button {
    width: 100%;
    font-size: 16px;
    color: #ffffff;
    background: linear-gradient(83deg,#ff068b 0%, #ff9705 80%);
    border-radius: 15px;
    display: flex;
    padding:10px;
      justify-content: flex-start;
    align-items: center;
   
    cursor: progressnpm;
  }
  
  button:hover {
    background-color: #1b295c;
  }
  .score-section{
    display: flex;
  align-items: center;
  font-size: 30px;
  color:white;
  font-weight: bold;

  }